"use client";

import { useState, useEffect } from "react";
import { usePrivy } from "@privy-io/react-auth";
import styles from "./page.module.css";
import SignIn from "../components/SignIn";
import ProfileModal from "../components/ProfileModal";
import { useWallet } from "@solana/wallet-adapter-react";
import { WalletMultiButton } from "@solana/wallet-adapter-react-ui";

export default function Home() {
  const { ready, authenticated, user, wallet, createWallet } = usePrivy();
  const [isProfileModalOpen, setIsProfileModalOpen] = useState(false);
  const [solanaAddress, setSolanaAddress] = useState<string | null>(null);
  const { connected } = useWallet();
  
  // Get the Privy-generated Solana wallet address
  useEffect(() => {
    const getSolanaWallet = async () => {
      if (authenticated && user) {
        try {
          console.log("Checking for Solana wallet...");
          // Get the user's embedded wallets
          const wallets = user.linkedAccounts.filter(
            (account) => account.type === "wallet"
          );
          
          console.log("User wallets:", wallets);
          
          // Find the Solana wallet
          const solanaWallet = wallets.find(
            (wallet) => wallet.walletClientType === "solana"
          );
          
          console.log("Solana wallet:", solanaWallet);
          
          if (solanaWallet && solanaWallet.address) {
            console.log("Found Solana address:", solanaWallet.address);
            setSolanaAddress(solanaWallet.address);
          } else {
            console.log("No Solana wallet found");
            
            // Try to create a Solana wallet if not found
            try {
              console.log("Attempting to create Solana wallet...");
              const newWallet = await createWallet({ chain: "solana" });
              console.log("Created new Solana wallet:", newWallet);
              if (newWallet && newWallet.address) {
                setSolanaAddress(newWallet.address);
              }
            } catch (createError) {
              console.error("Error creating Solana wallet:", createError);
            }
          }
        } catch (error) {
          console.error("Error getting Solana wallet:", error);
        }
      }
    };
    
    getSolanaWallet();
  }, [authenticated, user]);

  // If not ready yet, could show a loading state
  if (!ready) {
    return <div>Loading...</div>;
  }

  // If not signed in, show the sign-in page
  if (!authenticated) {
    return <SignIn />;
  }

  // Otherwise, show the dashboard
  return (
    <div className={styles.page}>
      <header className={styles.header}>
        <div className={styles.headerLeft}>
          <h1 className={styles.logo}>BANKR ON SOL</h1>
        </div>
        <div className={styles.headerRight}>
          <WalletMultiButton className={styles.walletModeBtn} />
          <button
            className={styles.profileBtn}
            onClick={() => setIsProfileModalOpen(true)}
          >
            👤
          </button>
        </div>
      </header>

      <div className={styles.disclaimer}>
        Bankr On SOL is currently in alpha and may occasionally make mistakes.
        Use at your own risk. At this stage, Bankr supports only Solana.
      </div>

      <main className={styles.main}>
        <div className={styles.activitySection}>
          <h2 className={styles.sectionTitle}>Activity</h2>
          <div className={styles.transaction}>
            <div className={styles.transactionDetails}>
              <span className={styles.arrow}>▶</span>
              Transfer TOBY to
              <div className={styles.address}>
                0x4e902e01d78300b0ba876562e99e66e929715e
              </div>
            </div>
            <button className={styles.checkBtn}>✓</button>
          </div>
        </div>

        <div className={styles.chatSection}>
          <div className={styles.chatArea}>
            <div className={styles.chatPlaceholder}>
              {/* Welcome! What do you want to do? */}
            </div>
          </div>
          <div className={styles.messageCounter}>
            0 / 10 free daily messages used
            <div className={styles.usageBar}>
              <div className={styles.usageProgress} style={{ width: "0%" }}>
                0.00%
              </div>
            </div>
          </div>
          <div className={styles.chatFooter}>
            Enjoy Bankr without limits
            <button className={styles.joinClubBtn}>Join Bankr Club</button>
          </div>
        </div>

        <div className={styles.walletSection}>
          <h2 className={styles.walletTitle}>Wallet ▼</h2>
          <div className={styles.walletInfo}>
            <div className={styles.walletProvider}>
              <div className={styles.baseIcon}>S</div> Solana
              <span className={styles.walletBalance}>--</span>
            </div>
            <div className={styles.tradingWallet}>
              {connected ? "External wallet:" : "Privy-generated wallet:"}
              <span className={styles.walletAddress}>
                {connected 
                  ? "Connected via Solana adapter" 
                  : solanaAddress 
                    ? `${solanaAddress.slice(0, 4)}...${solanaAddress.slice(-6)}` 
                    : "Loading..."}
              </span>
              <button 
                className={styles.copyBtn}
                onClick={() => {
                  if (solanaAddress) {
                    navigator.clipboard.writeText(solanaAddress);
                    alert("Address copied to clipboard!");
                  }
                }}
                disabled={!solanaAddress}
              >
                📋
              </button>
            </div>
            <div className={styles.ethBalance}>
              <span className={styles.ethIcon}>↑ SOL:</span>
              <span className={styles.ethAmount}>--</span>
              <span className={styles.ethValue}>--</span>
            </div>
            <div className={styles.assetsLabel}>Assets</div>
          </div>
        </div>
      </main>

      <ProfileModal
        isOpen={isProfileModalOpen}
        onClose={() => setIsProfileModalOpen(false)}
      />
    </div>
  );
}
